import React from 'react';
import Category from './category';
import './nav.css';
import { categories } from '../../constants';

class SideNavBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleCategorySelect = (category, e) => {
        console.log("handleCategorySelect called ....")
        this.props.updateCurrentCategory(category);
    }

    render() {
        return (
            <div className="navBar">
                <ul className="categoryList">
                    {
                        Object.keys(categories).map((category, index) => (
                            <li onClick={(e) => this.handleCategorySelect(category, e)}>
                                <Category name={category} key={category + "" + index} />
                            </li>
                        ))
                    }
                </ul>
            </div>
        );
    }
}

export default SideNavBar;