import logo from './logo.svg';
import './App.css';
import Header from './components/header/header';
import SideNavBar from './components/navigation/sideNavBar';
import MainContent from './components/content/mainContent';
import React from 'react';
import { appContext } from './themeContext';
import { categories } from './constants';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      productsInCart: [],
      totalCount: 0,
      currentCategory: "smarttv",
      newCategory: "smarttv",
      allProducts: []
    }
  }

  contextUpdater = (newProduct, newTotalCount, action) => {
    let totalCount = this.state.totalCount;
    if (action === "decrement") {
      totalCount = totalCount > 0 ? totalCount - 1 : totalCount;
    }
    else {
      totalCount = totalCount + 1;
    }

    let productsInCart = this.state.productsInCart;
    let doesExist = false;
    console.log("Products in cart length", productsInCart.length);
    for (let i = 0; i < productsInCart.length; i++) {
      console.log("products in Cart[i]", productsInCart[i])
      if (productsInCart[i].id === newProduct.id) {
        console.log("Inside if block");
        productsInCart[i].quantity++;
        doesExist = true;
      }
    }

    if (!doesExist) {
      productsInCart = [...productsInCart, { ...newProduct, quantity: 1 }]
    }

    console.log("Products in Cart ", productsInCart);
    this.setState({ totalCount, productsInCart })
  }

  updateCurrentCategory = (category) => {
    console.log("New Category Selected ....");
    this.setState({ newCategory: category, })
  }

  componentDidMount() {
    let allProducts = categories[this.state.currentCategory];
    this.setState({ allProducts })
  }

  componentDidUpdate() {
    if (this.state.newCategory !== this.state.currentCategory) {
      console.log("Going to fetch new Products for new Category")
      let allProducts = categories[this.state.newCategory];
      this.setState({ allProducts, currentCategory: this.state.newCategory })
    }
  }

  render() {
    const { contextUpdater } = this;
    const { productsInCart, totalCount } = this.state;
    return (
      <appContext.Provider value={{ productsInCart, totalCount, contextUpdater }}>
        <div className="App">
          <Header />
          <SideNavBar selectedCategory={this.state.currentCategory} updateCurrentCategory={this.updateCurrentCategory} />
          <MainContent selectedCategory={this.state.currentCategory} allProducts={this.state.allProducts} />
        </div>
      </appContext.Provider>
    );
  }
}

export default App;